.login-pic {
  width: 100%;
  display: block;
  background: #F6F7FB;
  height: 100%;
  position: relative;
  min-width: 1366px;
}
.login-pic .login-box {
  width: 555px;
  margin: 6% auto 0;
  position: absolute;
  left: 0;
  right: 0;
}
.login-pic .login-box .login-logo {
  text-align: center;
}
.login-pic .login-box .login-logo img {
  margin: 0 10px;
}
.login-pic .login-box .login-form {
  width: 100%;
  display: block;
  background: #FFFFFF;
  box-shadow: 0px 10px 10px rgba(107, 110, 137, 0.07);
  border-radius: 21px;
  padding: 50px 120px;
  margin: 8% 0 0;
}
.login-pic .login-box .login-form .title {
  font-style: normal;
  font-weight: normal;
  font-size: 36px;
  line-height: 49px;
  letter-spacing: 14.5px;
  color: #2A283D;
  text-align: center;
  display: block;
  margin-bottom: 10px;
}
.login-pic .login-box .login-form .p {
  font-style: normal;
  font-weight: normal;
  font-size: 18px;
  line-height: 25px;
  letter-spacing: 2px;
  color: #2A283D;
  opacity: 0.3;
  display: block;
  text-align: center;
}
.login-pic .login-box .login-form .ant-form {
  margin: 15% 0 0;
}
.login-pic .login-box .login-form .ant-form .ant-row .ant-col {
  display: block;
  width: 100%;
  text-align: left;
}
.login-pic .login-box .login-form .ant-form .ant-row .ant-form-item-label {
  font-style: normal;
  font-weight: normal;
  font-size: 12px;
  line-height: 16px;
  letter-spacing: 2px;
  color: #2A283D;
  opacity: 0.5;
  display: block;
  margin-bottom: 8px;
  text-indent: 12px;
}
.login-pic .login-box .login-form .ant-form .ant-row .ant-form-item-control-wrapper {
  margin-bottom: 20px;
}
.login-pic .login-box .login-form .ant-form .ant-row .ant-form-item-control-wrapper .ant-input {
  background: #E8ECF4;
  opacity: 0.8;
  border-radius: 5px;
}
.login-pic .login-box .login-form .ant-form .ant-row .ant-form-item-control-wrapper input:-webkit-autofill,
.login-pic .login-box .login-form .ant-form .ant-row .ant-form-item-control-wrapper textarea:-webkit-autofill,
.login-pic .login-box .login-form .ant-form .ant-row .ant-form-item-control-wrapper select:-webkit-autofill {
  -webkit-text-fill-color: #2A283D !important;
  -webkit-box-shadow: 0 0 0px 1000px transparent  inset !important;
  background-color: transparent;
  background-image: none;
  transition: background-color 50000s ease-in-out 0s;
}
.login-pic .login-box .login-form .ant-form .ant-row .ant-form-item-control-wrapper input {
  background-color: transparent;
}
.login-pic .login-box .login-form .ant-form .ant-col-offset-8 {
  margin-left: auto;
  margin-right: auto;
  margin-top: 15px;
}
.login-pic .login-box .login-form .ant-form .ant-col-offset-8 .ant-checkbox-wrapper {
  float: right;
}
.login-pic .login-box .login-form .ant-form .ant-col-offset-8 .ant-form-item-children {
  display: block;
  margin: 0 auto;
  text-align: center;
}
.login-pic .login-box .login-form .ant-form .ant-col-offset-8 .ant-btn-primary {
  width: 215px;
  height: 42px;
  background: #1A82FF;
  border-radius: 100px;
  text-align: center;
  margin: 0 auto;
}
.login-pic .login-box .login-form .ant-form .ant-col-offset-8 .ant-btn-primary span {
  font-style: normal;
  font-weight: normal;
  font-size: 17px;
  line-height: 23px;
  text-align: center;
  letter-spacing: 11.5px;
  color: #FFFFFF;
}
html {
  overflow-x: auto;
  overflow-y: auto;
}
